import React, { Component } from "react";
import $ from "jquery";
import "./App.scss";
import Header from "./components/Header";
import Footer from "./components/Footer";
import About from "./components/About";
import Projects from "./components/Projects";
import Skills from "./components/Skills";

import {
  Route,
  Routes,
  Link
} from "react-router-dom";

const linkStyle = {
  textDecoration: "none",
};

class App extends Component {

  constructor(props) {
    super();
    this.state = {
      foo: "bar",
      resumeData: {},
      sharedData: {},
    };
  }

  applyPickedLanguage(pickedLanguage) {
    document.documentElement.lang = pickedLanguage;
    var resumePath = `res_primaryLanguage.json`;
    this.loadResumeFromPath(resumePath);
  }

  componentDidMount() {
    this.loadSharedData();
    this.applyPickedLanguage(
      window.$primaryLanguage
    );
  }

  loadResumeFromPath(path) {
    $.ajax({
      url: path,
      dataType: "json",
      cache: false,
      success: function (data) {
        this.setState({ resumeData: data });
      }.bind(this),
      error: function (xhr, status, err) {
        alert(err);
      },
    });
  }

  loadSharedData() {
    $.ajax({
      url: `portfolio_shared_data.json`,
      dataType: "json",
      cache: false,
      success: function (data) {
        this.setState({ sharedData: data });
        document.title = `${this.state.sharedData.basic_info.name}`;
      }.bind(this),
      error: function (xhr, status, err) {
        alert(err);
      },
    });
  }
//https://stackoverflow.com/questions/55425363/render-reddit-embed-into-reactjs-component
  render() {
    return (
      <div>
        <Header sharedData={this.state.sharedData.basic_info} />
        <nav id="navhome">
        <ul>
          <Link style={linkStyle} to="/About"><li class="highlight parallelogram">About Me</li></Link>
          <Link style={linkStyle} to="/Skills"><li class="highlight parallelogram">Skills</li></Link>
          <Link style={linkStyle} to="/Projects"><li class="highlight parallelogram">My Projects</li></Link>
        </ul>
      </nav>
        
        <Routes>
          <Route path="/About" element=
          {
            <About
            resumeBasicInfo={this.state.resumeData.basic_info}
            sharedBasicInfo={this.state.sharedData.basic_info}
            />
          }
          />
          <Route path="/Skills" element=
          {
            <Skills
            sharedSkills={this.state.sharedData.skills}
            resumeBasicInfo={this.state.resumeData.basic_info}
            />
          }
          />
          <Route path="/Projects" element=
          {
            <Projects
            resumeProjects={this.state.resumeData.projects}
            resumeBasicInfo={this.state.resumeData.basic_info}
            />
          }
          />
        </Routes>

        <Footer sharedBasicInfo={this.state.sharedData.basic_info} />
      </div>
    );
  }
}

export default App;